import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { Router } from "@angular/router";
import { UserForRegisterDto } from "src/app/dtos/userForRegisterDto";
import { User } from "src/app/models/user";
import { AlertifyService } from "src/app/services/alertify.service";
import { AuthService } from "src/app/services/auth.service";
import { PageTitleService } from "src/app/services/page-title.service";
import { UserService } from "src/app/services/user.service";
import { HashingHelperService } from 'src/app/services/hashing-helper.service';

@Component({
  selector: "app-user-add",
  templateUrl: "./user-add.component.html",
  styleUrls: ["./user-add.component.css"],
})
export class UserAddComponent implements OnInit {
  userForm: FormGroup;
  newUser: User = new User();
  tokenUserId = parseInt(this.authService.getIdentity().nameidentifier);
  userId: number;

  constructor(
    private pageTitleService: PageTitleService,
    private fb: FormBuilder,
    private alertifyService: AlertifyService,
    private router: Router,
    private userService: UserService,
    private authService: AuthService
  ) {}

  ngOnInit(): void {
    this.pageTitleService.setPageTitle("Yeni Kullanc Ekle");
    this.userService.getUserById(this.tokenUserId).subscribe((user) => {
      this.userId = user["data"].userId;
      this.authService.updateUserName(user["data"].firstName + " " + user["data"].lastName);
      console.log(this.userId);
    });
    this.createUserForm();
  }

  createUserForm() {
    this.userForm = this.fb.group({
      firstName: ["", Validators.required],
      lastName: ["", Validators.required],
      email: ["", [Validators.required, Validators.email]],
      password: ["", Validators.required],
      role: ["", Validators.required],
      adres: ["", Validators.required],
    });
  }

  addUser() {
    if (this.userForm.valid) {
      this.alertifyService.confirm(
        "UYARI!",
        "Yeni kullanc eklemek istediinize emin misiniz?",
        () => {
          const newUser: UserForRegisterDto = {
            email: this.userForm.get("email").value,
            password: this.userForm.get("password").value,
            firstName: this.userForm.get("firstName").value,
            lastName: this.userForm.get("lastName").value,
            role: this.userForm.get("role").value,
            adres: this.userForm.get("adres").value,
          };

          this.authService.register(newUser, newUser.password).subscribe(
            (response: any) => {
              console.log("Kullanc Baaryla Eklendi:", response);
              this.alertifyService.success(
                "Kullanc Ekleme lemi Baaryla Gerekleti."
              );
              this.router.navigateByUrl("/kullanici-islemleri");
            },
            (error) => {
              console.error("Kullanc ekleme baarsz:", error);
              this.alertifyService.error(error);
            }
          );
        },
        () => {
          // Kullanc "Hayr" derse, ilemi iptal edin
          this.alertifyService.warning(
            "Kullanc Ekleme lemini ptal Ettiniz..."
          );
          console.log("Kullanc ekleme ilemi iptal edildi.");
        }
      );
    } else {
      // Form geerli deilse kullancya bir hata gsterin veya baka bir ilem yapn
      console.error("Form geerli deil. Kullanc eklenemedi.");
      this.alertifyService.error("Form geerli deil. Kullanc eklenemedi.");
    }
  }
}
