import { Component, OnInit } from "@angular/core";
import { UserService } from "../services/user.service";
import { UserReportService } from "../services/user-report.service";
import { User } from "../models/user";
import { FormGroup } from "@angular/forms";
import { AuthService } from "../services/auth.service";
import { PageTitleService } from "../services/page-title.service";
import { Router } from "@angular/router";
import { AlertifyService } from "../services/alertify.service";

@Component({
  selector: "app-users",
  templateUrl: "./users.component.html",
  styleUrls: ["./users.component.css"],
  providers: [UserService, UserReportService],
})
export class UsersComponent implements OnInit {
  users: User[];
  currentPage = 1;
  itemsPerPage = 10;
  selectedUsersSpecific: User[];
  selectedUsers: User[] = [];
  pageSize = 10;
  pagedUsers: User[];
  searchText: string = "";
  tokenUserId = parseInt(this.authService.getIdentity().nameidentifier);
  userId: number;

  userForm: FormGroup;

  constructor(
    private userService: UserService,
    private authService: AuthService,
    private userReportService: UserReportService,
    private pageTitleService: PageTitleService,
    private router: Router,
    private alertifyService: AlertifyService
  ) {
    this.selectedUsers = this.userService.getSelectedUsers();
    this.selectedUsersSpecific = [];
  }

  ngOnInit() {
    this.pageTitleService.setPageTitle("Kullanc lemleri");
    this.userService.getUserById(this.tokenUserId).subscribe((user) => {
      this.userId = user["data"].userId;
      this.authService.updateUserName(
        user["data"].firstName + " " + user["data"].lastName
      );
      console.log(this.userId);
      this.loadUsers();
    });
  }

  loadUsers() {
    this.userService.getAll().subscribe(
      (data) => {
        this.users = data;
        console.log("Tm veriler: ", this.users);
        this.users.sort((a, b) => a.userId - b.userId);

        this.updatePagedUsers();
        this.selectedUsers = this.userService.getSelectedUsers();
        console.log("Seili Kullanclar: ", this.selectedUsers);
      },
      (error) => {
        console.error("Veri alnamad: ", error);
      }
    );
  }
  navigateToAddUser() {
    this.router.navigateByUrl("/kullanici-islemleri/user-add");
  }

  navigateToUpdateUser() {
    if (this.selectedUsers.length === 1) {
      const selectedUser = this.selectedUsers[0];
      // Doru kullanc ID'sini alarak gncelleme sayfasna ynlendirin
      this.router.navigate(["kullanici-islemleri/user-update"], {
        queryParams: { id: selectedUser.userId },
      });
    } else {
      this.alertifyService.error(
        "Ltfen gncellemek iin sadece bir adet kullanc sein"
      );
    }
  }

  search() {
    if (this.searchText.trim() === "") {
      this.pagedUsers = this.users.slice(0, this.itemsPerPage);
    } else {
      const filteredUsers = this.users.filter((user) => {
        return (
          (user.firstName &&
            user.firstName
              .toLowerCase()
              .includes(this.searchText.toLowerCase())) ||
          (user.lastName &&
            user.lastName
              .toLowerCase()
              .includes(this.searchText.toLowerCase())) ||
          (user.email &&
            user.email.toLowerCase().includes(this.searchText.toLowerCase())) ||
          (user.role &&
            user.role.toLowerCase().includes(this.searchText.toLowerCase())) ||
          (user.adres &&
            user.adres.toLowerCase().includes(this.searchText.toLowerCase()))
        );
      });

      this.pagedUsers = filteredUsers.slice(0, this.itemsPerPage);
    }
    this.currentPage = 1;
  }

  changePage(event) {
    this.currentPage = event.pageIndex + 1;
    this.updatePagedUsers();

    this.selectedUsers = [];
    this.selectedUsersSpecific = [];
    this.userService.setSelectedUsers(this.selectedUsers);
  }

  onCheckboxClicked(event: any, user: User) {
    if (event.target.checked) {
      console.log(user);
      this.selectedUsers.push(user);
      this.selectedUsersSpecific.push(user);
      this.userService.setSelectedUsers(this.selectedUsers);
      console.log(this.selectedUsers[0].userId);
    } else {
      const index = this.selectedUsers.findIndex(
        (item) => item.userId === user.userId
      );
      if (index !== -1) {
        this.selectedUsers.splice(index, 1);
        this.selectedUsersSpecific.splice(index, 1); // lgili tanmaz zel listeden de karn
        this.userService.setSelectedUsers(this.selectedUsers);
      }
    }
  }

  generateReport() {
    if (this.selectedUsers.length !== 1) {
      this.alertifyService.error(
        "Ltfen raporlamak iin en az bir kullanc sein."
      );
      return;
    }
    //console.log(this.selectedUsers.length);
    const dataToExport = this.selectedUsers.map((user) => {
      return {
        USERID: user.userId,
        AD: user.firstName,
        SOYAD: user.lastName,
        "E-MAL": user.email,
        ROL: user.role,
        ADRES: user.adres,
      };
    });
    this.userReportService.exportToExcel(dataToExport, "kullanicilar-rapor");
    this.alertifyService.success(
      "Kullanc bilgileri raporlar baaryla iletildi"
    );
  }

  deleteSelectedUsers() {
    if (this.selectedUsers.length > 0) {
      this.alertifyService.confirm(
        "DKKAT!",
        "Seili kullanc veya kullanclara ait tm bilgileri silmek istediinize emin misiniz?",
        () => {
          const deletionPromises = this.selectedUsers.map((user) => {
            console.log(user.userId);
            return this.userService
              .deleteUser(user.userId)
              .toPromise()
              .then(() => true) // Baarl silme ilemi
              .catch(() => false); // Silme hatas
          });

          Promise.all(deletionPromises)
            .then((results) => {
              results.forEach((result, index) => {
                if (result) {
                  // Baar mesaj gster
                  this.alertifyService.success(
                    `Kullanc ID ${this.selectedUsers[index].userId} baaryla silindi.`
                  );
                  // Kullanclar yerel listeden kaldr
                  this.users = this.users.filter(
                    (item) => item.userId !== this.selectedUsers[index].userId
                  );
                  this.updatePagedUsers();
                } else {
                  // Hata mesaj gster
                  this.alertifyService.error(
                    `Kullanc ID ${this.selectedUsers[index].userId} silinemedi.`
                  );
                }
              });

              this.selectedUsers = [];
              this.selectedUsersSpecific = [];
            })
            .catch((error) => {
              console.error("Silme ilemi srasnda hata olutu: ", error);
              this.alertifyService.error(
                "Silme ilemi srasnda bir hata olutu."
              );
            });
        },
        () => {
          // Kullanc HAYIR derse, ilemi iptal edin
          this.alertifyService.warning(
            "Kullanc Silme lemini ptal Ettiniz..."
          );
        }
      );
    } else {
      this.alertifyService.error(
        "Ltfen silmek iin en az bir kullanc sein."
      );
    }
  }

  updatePagedUsers() {
    const startIndex = (this.currentPage - 1) * this.itemsPerPage;
    const endIndex = startIndex + this.itemsPerPage;

    this.pagedUsers = this.users.slice(startIndex, endIndex);
  }
}
