import { Injectable } from "@angular/core";
import { TokenInfo } from "../models/token-info";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Router } from "@angular/router";
import { AlertifyService } from "./alertify.service";
import { BehaviorSubject, Observable, throwError } from "rxjs";
import { catchError, map } from "rxjs/operators";
import { JwtConfigService } from "./jwt-config.service";
import { JwtHelperService } from "@auth0/angular-jwt";
import { UserForLoginDto } from "../dtos/userForLoginDto";
import { UserForRegisterDto } from "../dtos/userForRegisterDto";

@Injectable({
  providedIn: "root",
})
export class AuthService {
  private apiUrl = "https://localhost:44390/api/auth";
  userToken: any;
  decodedToken: TokenInfo;
  jwtHelper: JwtHelperService = new JwtHelperService();
  TOKEN_KEY = "token";
  IsUserLogged = false;
  userTokenDatas = [];

  constructor(
    private httpClient: HttpClient,
    private router: Router,
    private alertifyService: AlertifyService,
    private jwtConfigService: JwtConfigService
  ) {}
  private userNameSubject = new BehaviorSubject<string | null>(null);
  userName$ = this.userNameSubject.asObservable();

  // Kullanc adn gncellemek iin bu fonksiyonu kullanlr
  updateUserName(userName: string | null) {
    this.userNameSubject.next(userName);
  }
  getJwtConfig() {
    return this.jwtConfigService.getJwtConfig();
  }

  getIdentity(): TokenInfo | null {
    if (this.loggedIn()) {
      const token = this.token;
      const decoded = this.jwtHelper.decodeToken(token);
      // TokenInfo arayzne uygun olarak kullanc bilgilerini al
      const identity: TokenInfo = {
        nameidentifier:
          decoded[
            "http://schemas.xmlsoap.org/ws/2005/05/identity/claims/nameidentifier"
          ],
        email: decoded["email"],
        name: decoded[
          "http://schemas.xmlsoap.org/ws/2005/05/identity/claims/name"
        ],
        role: decoded[
          "http://schemas.microsoft.com/ws/2008/06/identity/claims/role"
        ],
        nbf: decoded["nbf"],
        exp: decoded["exp"],
        iss: decoded["iss"],
        aud: decoded["aud"],
      };
      return identity;
    }
    return null;
  }

  login(userForLoginDto: UserForLoginDto): void {
    const headers = new HttpHeaders({
      "Content-Type": "application/json",
      "Authorization": `Bearer ${this.token}`
    });

    this.httpClient
      .post(this.apiUrl + "/login", userForLoginDto, { headers: headers })
      .subscribe(
        (data: any) => {
          const responseData = data["data"];
          this.saveToken(responseData.token); // API'den gelen token'i kaydedin
          this.userToken = responseData;
          console.log(responseData);
          console.log(this.jwtHelper.decodeToken(responseData.token));
          this.decodedToken = this.jwtHelper.decodeToken(
            responseData.token
          ) as TokenInfo;
          this.alertifyService.success("Sisteme giri yapld");
          this.router.navigateByUrl("/tasinmaz");
          this.IsUserLogged = true;

          //this.updateUserName(this.getIdentity().name);
        },
        (error: any) => {
          console.log(error);
          this.alertifyService.error("Kullanc ad veya ifre hatal");
        }
      );
  }

  register(
    userForRegister: UserForRegisterDto,
    password: string
  ): Observable<any> {
    const headers = new HttpHeaders({
      "Content-Type": "application/json",
      "Authorization": `Bearer ${this.token}`
    });
    const body = {
      email: userForRegister.email,
      password: password,
      firstName: userForRegister.firstName,
      lastName: userForRegister.lastName,
      role: userForRegister.role,
      adres: userForRegister.adres,
    };

    return this.httpClient
      .post(`${this.apiUrl}/register`, body, { headers: headers })
      .pipe(
        map((response: any) => {
          console.log("Kullanc baaryla eklendi");
          return response; // API'den dnen veriyi geri dndr
        }),
        catchError((error: any) => {
          // Kayt baarszsa
          console.error("Kullanc kayd baarsz: " + error);
          throw error;
        })
      );
  }

  isAdmin(): boolean {
    try {
      const role = this.getIdentity().role;

      if (role && role === "Admin") {
        return true;
      } else {
        return false;
      }
    } catch (error) {
      // Rol bilgisini bulamadmzda, konsola hata mesaj yazmayn ve kullancy giri ekranna ynlendirin
      this.router.navigate(["auth/login"]); // Giri ekrannn yolunu uygun ekilde gncelleyin
      return false;
    }
  }

  saveToken(token) {
    localStorage.setItem(this.TOKEN_KEY, token);
  }

  logOut() {
    this.alertifyService.confirm(
      "UYARI!",
      "k yapmak istediinize emin misiniz?",
      () => {
        localStorage.removeItem(this.TOKEN_KEY);
        this.alertifyService.success("Sistemden k yapld");
        this.IsUserLogged = false;
        this.router.navigateByUrl("auth/login");
        console.log(this.TOKEN_KEY);
      },
      () => {
        this.alertifyService.error("k ilemi iptal edildi");
      }
    );
  }
  logoutForGuard() {
    localStorage.removeItem(this.TOKEN_KEY);
    this.router.navigateByUrl("auth/login");
    this.IsUserLogged = false;
  }

  loggedIn(): boolean {
    return localStorage.getItem(this.TOKEN_KEY) !== null
      ? (this.IsUserLogged = true)
      : false;
  }

  get token(): string | null {
    return localStorage.getItem(this.TOKEN_KEY);
  }

  refreshToken(): Observable<any> {
    const refreshToken = localStorage.getItem("refreshToken");
    const headers = new HttpHeaders({
      "Content-Type": "application/json",
      "Authorization": `Bearer ${this.token}`
    });

    return this.httpClient
      .post<any>(
        `${this.apiUrl}/refresh-token`,
        { refreshToken },
        { headers: headers }
      )
      .pipe(
        map((data) => {
          // Assuming the API returns a new access token, update it in the local storage
          this.saveToken(data.token);
          return data;
        }),
        catchError((error) => {
          return throwError(error);
        })
      );
  }

  isTokenExpired(): boolean {
    const token = this.token;
    if (token) {
      console.log(this.jwtHelper.isTokenExpired(token));
      return this.jwtHelper.isTokenExpired(token);
    }
    return true;
  }
}
