ackground-color: #3498db;\r\n    color: #fff;\r\n  }\r\n.report-button {\r\n    background-color: #17b83f;\r\n    color: #fff;\r\n  }\r\n.action-button:hover {\r\n    filter: brightness(1.2);\r\n  }\r\n/* Tanmaz bulunamad mesaj stilini zelletirme */\r\n.no-data-message {\r\n    text-align: center; /* Metni ortala */\r\n    font-size: 18px; /* Metin boyutu */\r\n    margin-top: 20px; /* st boluk */\r\n    padding: 10px; /*  boluk */\r\n    background-color: #fff; /* Arka plan rengi */\r\n    border: 1px solid #ddd; /* Kenarlk */\r\n    border-radius: 5px; /* Ke yuvarlama */\r\n    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); /* Glge efekti */\r\n  }\r\n:host {\r\n    background-color: #2b0e35; /* Arka plan rengi (rnek olarak mavi) */\r\n    background-size: cover;\r\n    background-repeat: no-repeat;\r\n    background-attachment: fixed;\r\n  }\r\n/*# sourceMappingURL=data:application/json;base64, */"