module.exports = "/* Arama alan stilini zelletirme */\r\n.container {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  height: 60vh; /* Sayfann tamamn kaplar */\r\n  width: 100%;\r\n}\r\n.row{\r\n  width: 50%;\r\n}\r\n.input-group-append{\r\n  color: #7278ad;\r\n}\r\n.custom-search {\r\n  background-color: #2b0e35;\r\n  border-radius: 10px;\r\n  padding: 10px;\r\n  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);\r\n  width: 100%; /* stenilen genilik */\r\n  max-width: 700px; /* Maksimum genilik */\r\n}\r\n/* Tablo stilleri */\r\n.table {\r\n  align-items: center;\r\n  justify-content: center;\r\n  max-width: 1200px;\r\n  background-color: #f0f0f0;\r\n  border: 1px solid #ddd;\r\n  border-collapse: collapse;\r\n  border-radius: 5px;\r\n  width: 100%;\r\n}\r\n.table-responsive {\r\n  max-width: 100%; /* Tablo ieriinin maksimum genilii ayarland */\r\n  overflow-x: auto; /* Yatay kaydrma ubuunun grnmesini salar */\r\n}\r\n/* Tablo balk stilleri */\r\n.table thead {\r\n  background-color: #997fb8;\r\n  color: #fff;\r\n}\r\n/* Tablo balk hcreleri */\r\n.table th {\r\n  padding: 12px;\r\n  text-align: center;\r\n  background-color: #2b0e35; /* Updated background color */\r\n  color: #fff;\r\n}\r\n/* Tablo veri hcreleri */\r\n.table td {\r\n  padding: 10px;\r\n  text-align: center;\r\n  border-top: 1px solid #ddd;\r\n}\r\n/* Zebra izgili tablo (opsiyonel) */\r\n.table tbody tr:nth-child(even) {\r\n  background-color: #7278ad;\r\n}\r\n/* Hcre ierii stilini zelletirme (rnek olarak) */\r\n.table td {\r\n  color: #333;\r\n  background-color: #f0f0f0;\r\n}\r\n/* Seili satr stilini zelletirme (opsiyonel) */\r\n.table tbody tr.selected {\r\n  background-color: #ffffcc;\r\n}\r\n/* Dme stilini zelletirme */\r\n.action-button {\r\n  margin: 5px;\r\n  padding: 10px 20px;\r\n  font-size: 16px;\r\n  border-radius: 15px;\r\n  cursor: pointer;\r\n  transition: background-color 0.3s;\r\n}\r\n.action-button:disabled {\r\n  background-color: #ccc;\r\n  cursor: not-allowed;\r\n}\r\n.add-button {\r\n  background-color: #7278ad;\r\n  color: #fff;\r\n}\r\n.delete-button {\r\n  background-color: #d31500;\r\n  color: #fff;\r\n}\r\n.edit-button {\r\n  background-color: #3498db;\r\n  color: #fff;\r\n}\r\n.report-button {\r\n  background-color: #17b83f;\r\n  color: #fff;\r\n}\r\n.action-button:hover {\r\n  filter: brightness(1.2);\r\n}\r\n/* Tanmaz bulunamad mesaj stilini zelletirme */\r\n.no-data-message {\r\n  text-align: center;\r\n  font-size: 18px;\r\n  margin-top: 20px;\r\n  padding: 10px;\r\n  background-color: #fff;\r\n  border: 1px solid #ddd;\r\n  border-radius: 5px;\r\n  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */"