import { ChangeDetectorRef, Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { PageTitleService } from '../services/page-title.service';
import { AuthService } from '../services/auth.service';

@Component({
  selector: 'app-nav',
  templateUrl: './nav.component.html',
  styleUrls: ['./nav.component.css']
})
export class NavComponent implements OnInit, OnDestroy {
  pageTitle: string = 'Mevcut Tanmazlar Listesi';
  private pageTitleSubscription: Subscription;
  isLoggedIn: boolean = false;
  isAdmin: boolean = false;


  constructor(
    private pageTitleService: PageTitleService,
    private cdr: ChangeDetectorRef,
   private authService: AuthService
  ) { }

  ngOnInit() {
    this.pageTitleSubscription = this.pageTitleService.pageTitle$.subscribe(title => {
      this.pageTitle = title;
      this.cdr.detectChanges();
    });

    this.authService.userName$.subscribe(userName => {
      this.isLoggedIn = !!userName; // Kullanc ad varsa giri yaplm demektir
      this.isAdmin = this.authService.isAdmin(); // Admin rolne sahipse true dner
    });
  }

  ngOnDestroy() {
    this.pageTitleSubscription.unsubscribe(); // Abonelii burada iptal et
  }
  }
